* {
  cursor: none;
  text-shadow: 0 0 10px #00000080;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: inherit;
}

body {
  color: #d3d3d3;
  height: 1px;
  min-height: 100vh;
  background-color: #181a1b;
  font-family: Roboto, sans-serif;
}

main {
  width: 100%;
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

h1, h2, h3 {
  font-family: Poppins, sans-serif;
}

section {
  height: 100%;
  width: 100%;
  flex-shrink: 0;
}

ul {
  list-style: none;
}

::-webkit-scrollbar {
  height: 1px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to right, #0000, #d3d3d3, #0000);
}

.hidden {
  display: none !important;
}

.noscroll {
  width: 100%;
  position: fixed;
  top: 0;
  overflow: hidden;
}

#body {
  min-height: 100%;
  width: 100%;
  flex-direction: column;
  display: flex;
}

@media screen and (min-width: 996px) {
  body {
    overflow: hidden;
  }

  main {
    scrollbar-width: none;
    scroll-behavior: smooth;
    flex-direction: row;
    align-items: center;
    overflow-x: scroll;
    overflow-y: hidden;
  }

  #body {
    -webkit-backdrop-filter: blur(150px);
    backdrop-filter: blur(150px);
  }
}

@layer base {
  :root, :host {
    --fa-font-brands: normal 400 1em / 1 "Font Awesome 6 Brands";
    --fa-font-regular: normal 400 1em / 1 "Font Awesome 6 Free";
    --fa-font-solid: normal 900 1em / 1 "Font Awesome 6 Free";
  }

  .fa {
    font-family: var(--fa-style-family, "Font Awesome 6 Free");
    font-weight: var(--fa-style, 900);
  }

  .fa, .fas, .fa-solid, .far, .fa-regular, .fab, .fa-brands {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: var(--fa-display, inline-block);
    font-variant: normal;
    text-rendering: auto;
    font-style: normal;
    line-height: 1;
  }

  @font-face {
    font-family: "Font Awesome 6 Brands";
    font-style: normal;
    font-weight: 400;
    font-display: block;
    src: local("Font Awesome 6 Brands"), url("fa-brands-400.91a08dbe.woff2") format("woff2");
  }

  .fab, .fa-brands {
    font-family: "Font Awesome 6 Brands";
    font-weight: 400;
  }

  @font-face {
    font-family: "Font Awesome 6 Free";
    font-style: normal;
    font-weight: 400;
    font-display: block;
    src: local("Font Awesome 6 Free"), url("fa-regular-400.34250813.woff2") format("woff2");
  }

  .far, .fa-regular {
    font-family: "Font Awesome 6 Free";
    font-weight: 400;
  }

  @font-face {
    font-family: "Font Awesome 6 Free";
    font-style: normal;
    font-weight: 900;
    font-display: block;
    src: local("Font Awesome 6 Free"), url("fa-solid-900.977e2c2d.woff2") format("woff2");
  }

  .fas, .fa-solid {
    font-family: "Font Awesome 6 Free";
    font-weight: 900;
  }

  .fa-github:before {
    content: "";
  }

  .fa-globe:before {
    content: "";
  }

  .fa-linkedin-in:before {
    content: "";
  }

  .fa-youtube:before {
    content: "";
  }

  .fa-xmark:before {
    content: "";
  }
}

#about .content {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

#about .content nav {
  flex-wrap: wrap;
  gap: 20px;
  margin: 10px 0 20px;
  display: flex;
}

#about .content nav a {
  min-width: 75px;
  text-align: center;
  background-color: #9370db;
  border-radius: 10px;
  padding: .5rem 1rem;
  font-size: 12px;
  transition: all .5s;
  box-shadow: 0 0 10px #00000080;
}

@media screen and (min-width: 996px) {
  #about .content nav a {
    background-color: #222425;
  }
}

#about .content nav a:hover {
  background-color: #9370db;
  transform: scale(1.1);
}

#contact form {
  flex-direction: column;
  gap: 20px;
  margin-top: 50px;
  display: flex;
}

#contact form .personnal-infos {
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 30px;
  display: flex;
}

#contact form input, #contact form textarea {
  color: inherit;
  font: inherit;
  background-color: #222425;
  border: none;
  border-radius: 10px;
  padding: 15px;
  transition: box-shadow .3s;
  box-shadow: inset 0 0 #000, 0 0 10px #00000080;
}

#contact form input:focus, #contact form textarea:focus {
  outline: none;
  box-shadow: inset 0 0 10px #000, 0 0 10px #00000080;
}

#contact form input:-webkit-autofill, #contact form textarea:-webkit-autofill {
  transition: background-color 600000s, color 600000s;
}

#contact form textarea {
  min-height: 150px;
}

#contact form #notice p {
  font-size: 16px;
}

#contact form #notice p.error {
  color: #742f2f;
}

#contact .content {
  max-width: 100%;
}

#contact .content a, #contact .content input[type="submit"] {
  appearance: none;
  width: max-content;
  background-color: #9370db;
  border-radius: 10px;
  padding: .5rem 1rem;
  transition: all .3s;
}

#contact .content a:hover, #contact .content input[type="submit"]:hover {
  background-color: #9370db;
  box-shadow: 0 0 10px #00000080;
}

#contact .content h3 {
  margin: -5px 0 20px;
}

@media screen and (min-width: 996px) {
  #contact form .personnal-infos {
    flex-direction: row;
    margin-bottom: 0;
  }

  #contact form .personnal-infos input {
    width: calc(33% - 13.33px);
  }

  #contact form input, #contact form textarea {
    padding: .5rem 1rem;
  }

  #contact .content a, #contact .content input[type="submit"] {
    background-color: #222425;
  }
}

#projects .content {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

#projects .content .project {
  width: 100%;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 10px #00000080;
}

#projects .content .project:first-of-type {
  flex-direction: column-reverse;
}

#projects .content .project img {
  max-width: 400px;
  object-fit: cover;
  object-position: center;
  width: 100%;
  flex-shrink: 0;
}

#projects .content .project .details {
  background-color: #181a1b;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

#projects .content .project .details p {
  flex-grow: 1;
}

#projects .content .project #external {
  align-self: flex-end;
  gap: 20px;
  font-size: 25px;
  display: flex;
}

#projects .content .project #external a {
  transition: transform .3s;
}

#projects .content .project #external a:hover {
  transform: scale(1.2);
}

@media screen and (min-width: 576px) {
  #projects .content .project, #projects .content .project:first-of-type {
    flex-direction: row;
  }

  #projects .content .project img {
    height: 300px;
  }
}

@media screen and (min-width: 996px) {
  #projects .content .project {
    border-radius: none;
    box-shadow: none;
    gap: 20px;
  }

  #projects .content .project img, #projects .content .project .details {
    background-color: #222425;
    border-radius: 10px;
    box-shadow: 0 0 10px #00000080;
  }

  #projects .content .project img {
    height: min(50vh - 110px, 300px);
    width: auto;
  }
}

#projects .content #artify h3 {
